@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.div-background{
    background-size: cover;
    background-position: center;
    height: 90%;
    top: 57px;
    width: 100%;
    position: absolute
}

.form{
    min-width: 490px;
    width: 43%;   
    text-align: center;
    position: absolute;
    top: 27%;
    left: 29%;
}

.div-home-containers{
    padding: 10px 0px;
}

#input-search{
    font-size: x-large;
    width: 100%;
    padding: 10px 40px 10px 10px; /* Espacio para el ícono a la derecha */
    font-size: 16px;
    box-sizing: border-box; /* Asegura que el padding se mantenga dentro del input */
}


.input-container {
    position: relative;
    width: 100%; /* Ajusta según tus necesidades */
  }
  
  .search-icon {
    position: absolute;
    right: 10px; /* Ajusta la distancia del ícono al borde derecho */
    top: 50%;
    transform: translateY(-50%); /* Centra el ícono verticalmente dentro del input */
    color: gray;
    font-size: 20px; /* Tamaño del ícono */
    pointer-events: none; /* Para que no interfiera con la entrada de texto */
  }
.navbar{
    position: fixed;
    width: 100%;
    z-index: 10;
}

.bg-body-tertiary {
    --bs-bg-opacity: 1;
    background-color: rgb(255 255 255) !important;
    border-bottom: solid thin;
    border-color: #dee2e6;
}

.nav-link{
    border-radius: 10%;
    padding: 5px 25px !important;
}

.active{
    background-color: #e5e7eb;
}
.img-card{
    width: 18rem;
    margin-bottom: 30px;
    height: -moz-fit-content;
    height: fit-content;
}
.container-results{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    cursor: pointer;
}

.top-gradient{
    background-repeat: no-repeat;
    background-size: cover;
    height: 85px;
}

.form-search{
    height: 90px;
    position: relative;
    background: white;

    display: flex;
    justify-content: center;
    width: 100%;
}

.search-in-result-page{    
    width: 50%;
    min-width: 500px;
    top: -30px;
    position: absolute;
}

.input-search{
    padding: 13px;
}

.search-icon {
    position: absolute;
    right: 25px; /* Ajusta la distancia del ícono al borde derecho */
    top: 50%;
    transform: translateY(-50%); /* Centra el ícono verticalmente dentro del input */
    color: gray;
    font-size: 20px; /* Tamaño del ícono */
    pointer-events: none; /* Para que no interfiera con la entrada de texto */
}
.custom-row{
    background-color: white;
    padding: 10px;
    border-radius: 5px;
    width: 100%;
}

.custom-row *{
    cursor: pointer;
}

.custom-row.active{
    background-color: #e5e7eb;
}

.custom-row.active *:not(.col-remove){
    cursor: default;
}

.div-small-image{
    width: 100px;
    display: flex;
    justify-content: center;
}

.div-small-image > img{
    border-radius: 6px;
}

.img-col{
    display: contents;
}

.img-col img{
    height: 60px;
}

.info-col-title{
    font-weight: bold;
}

.col-remove{
    text-align: right;
    align-content: center;
    font-size: small;
    cursor: pointer;
}

.modal-container{
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;   
    z-index: 100; 
}

.modal-background{
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    position: absolute;
    background: #d0d1d475;
    z-index: 0;    
}

.modal-form{
    width: 38%;
    min-width: 300px;
    background: white;
    padding: 1rem 3rem;
    border-radius: 5px;
    position: absolute;
    height: 90%;
    top: 5%;
}

.modal-form.h4{
    font-weight: bold;
}

.list-collection-modal-container{
    height: 75%;
    overflow: auto;
    position: absolute;
    width: 89%;
}

.info-add-collection{
    display: flex;
    align-items: center;
}

.p-info-count-matches{
    padding-top: 1rem;
}

.btn-new-collection{
    text-align: end;
}

.btn-new-collection label{
    cursor: pointer;
}

.btn-new-collection label:hover{
    background-color: #e5e7eb;
}

.list-collection-modal{
    height: auto;
    overflow: auto;
    position: absolute;
    width: -webkit-fill-available;
}
.container-details-image{
    padding: 4em;
    width: 100%;
}

.detail-fullimage{
    width: 100%;
}

.user-avatar{
    border-radius: 100%;
    margin: 0px 10px;
}

.user-name-span{
    font-weight: 600;
}

.div-user-info{
    margin-left: .5rem;
}

.div-user-info p{
    margin-top: 1rem;
}

.my-grey-button{
    background-color: #e5e7eb;
    width: auto;
    margin: 0px 1rem;
}

.my-grey-button img{
    margin-right: 5px;
    top: -2px;
    position: static;
}

.collections-container{
    padding-top: 2.5rem;
}

.list-collection{
    overflow-y: auto;
    height: 80%;
    position: absolute;
    width: calc(50% - 100px);
    overflow-x: hidden;
}
.collection-card{
    margin-top: 40px;
    justify-items: center;
}

.collection-card > .row{
    flex-wrap: nowrap;
    width: 420px;
    cursor: pointer;
}

.lbl-nombre{
    font-weight: bold;
}


.una-foto{
    object-fit: cover;
    width: 500px;
}


.img-left,
.img-right-1,
.img-right-2{
    padding: 0px
}

.img-left > .una-foto{
    width: 420px;
    height: 250px;
    object-fit: cover;
    border-radius: 10px;
}

.col.img-left > .tres-fotos,
.img-left > .dos-fotos{
    border-radius: 10px 0px 0px 10px;
}

.col.img-right-1 > img{
    border-radius: 0px 10px 10px 0px;
}


.col.img-right-2 > .row > .img-1{
    border-radius: 0px 10px 0px 0px;
    border-left: solid white;
}

.col.img-right-2 > .row > .img-2{
    border-radius: 0px 0px 10px 0px;
    border-left: solid white;
    border-top: solid white;
}


.col.img-left > .dos-fotos,
.col.img-right-1 > img{
    height: 250px;
    object-fit: cover;
    width: 210px;
    border-left: solid white;
}

.img-left img.tres-fotos{
    width: 320px;
    height: 250px;
    object-fit: cover;
}


.img-right-2 > .row > img{
    height: 125px;
    width: 117px;
    object-fit: cover;
    padding: 0px;
}

.img-right-2 > .row{
    width: 150px;
}

.img-right-2{
    flex: none;
    width: 126px;
    justify-content: center;
}

.tres-fotos{
    width: 380px;
    display: flex;
}

.lbl-info{
    color: #aba8a8;
}
.collection-container-page{
    padding: 0px 5%;
    width: 100%;
}

.title-container{
    display: flex;
    justify-content: center;
    margin: 2rem 0px;
    font-family: 'Be Vietnam Pro', sans-serif;
}

.title{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title > label{
    text-align: center;
}

.texto-gradiente {
    width: auto;
    font-size: 48px;
    background: url('/gradiend-bg@2x.png') no-repeat;
    background-size: 100% 100%;
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    -webkit-mask-image: url('/gradiend-bg@2x.png');
    mask-image: url('/gradiend-bg@2x.png');
}
  

.bold-underline{
    text-decoration: underline;
    font-weight: 500;
}
.alert-compomnent{
    position: relative;
    right: 0px;
    z-index: 100000;
}

.rest-time{
    display: block;
    position: absolute;
    bottom: 0;
    right: 0px;
    height: 5px;
    transition: "width 0.1s linear";
}
.error-container{
    position: absolute;
    right: 0px;
    top: 0px;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: flex-end;
}
.spinner-background{
    top: 0px;
    width: 100%;
    height: 100%;
    position: fixed;
    background: #ffffffdb;
    z-index: 100;
}

.spinner-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    position: fixed;
    width: 100%;
    justify-content: center;
    z-index: 1000;
}

.spinner-border{
    top: 47.5%;
    left: 46.5%;
    --bs-spinner-width: 5rem;
    --bs-spinner-height: 5rem;
    --bs-spinner-border-width: 0.4em;
    border-color: #0a3d81;
    border-right-color: #fefefe00;
}

.spinner-label-container{
    display: contents;
}

.spinner-label-1{
    font-weight: bold;
}
.navbar-space{
  height: 57px;
}

.App {
  text-align: center; 
}

h1, h4, label, button, .nav-link, p{
  font-family: 'Be Vietnam Pro', sans-serif;
}
